export default {
	data() {
		return {
			type:'password',
			isuser:false,//用户名是否可用
			isemail:false,//邮箱是否占用
			invalid:false,//邮箱是否符合
			useremploy:false,//用户名是否被占用
			emailemploy:false,//邮箱是否被占用
			lookurl:"../../static/img/eye.jpg",
			isok:false,//输入正确变色注册按钮
			UserEmail:'',//邮箱
			UserName:'',//注册名
			UserPassword:'',//密码
		}
	},
	methods: {
		//跳转到注册页面
		toSignUp:function(){
			uni.navigateTo({
			    url: '../register/register',
			});
		},
		//密码显示与隐藏
		looks:function(){
			if(this.look){
				this.type="password";
				this.look=!this.look;
				this.lookurl='../../static/img/taiyang.jpg';
			}else{
				this.type="text";
				this.look=!this.look;
				this.lookurl='../../static/img/eye.jpg';
			}
		},
		// 获取用户名
		getUserName:function(e){
			// console.log(e);
			// let reg = /^[a-zA-Z0-9_-]{4,16}$/;
			let reg = /^.{2,16}$/;
			this.UserName = e.detail.value;
			if(this.UserName.length>0){
				if(reg.test(this.UserName)){
					// console.log("用户名正确");
					// console.log(this.UserName);
					this.register();
					this.isuser = true;
				}else{
					// console.log("用户名不正确");
				}
			}
		},
		//判断是否是邮箱
		getUserEmail:function(e){
			let reg=/^\w{3,}(\.\w+)*@[A-z0-9]+(\.[A-z]{2,5}){1,2}$/;
			// console.log(e.detail.value);
			this.UserEmail=e.detail.value;
			console.log(this.UserEmail);
			if(this.UserEmail.length>0){
				if(reg.test(this.UserEmail)){
					// console.log("正确");
					this.register();
					this.isemail = true;
					this.invalid = false;
				}else{
					// console.log("不正确");
					this.invalid = true;
					this.isemail = false;
				}
			}
		},
		// 获取密码
		getUserPassword:function(e){
			//至少6-16个字符，至少1个大写字母，1个小写字母和1个数字，其他可以是任意字符：
			let reg =  /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[^]{6,16}$/;
			this.UserPassword = e.detail.value;
			console.log(this.UserPassword);
			if(this.UserPassword.length>0){
				if(reg.test(this.UserPassword)){
					// console.log("密码合格");
					this.register();
					// console.log(this.UserPassword);
				}else{
					// console.log("密码不合格");
				}
			}
		},
		// 判断注册提交
		register:function(){
			if(this.UserName && this.UserEmail && this.UserPassword){
				uni.request({
					url:'http://tp5.1.com/api/user/add',
					data:{
						email:this.UserEmail,
						name:this.UserName,
						password:this.UserPassword,
					},
					method:'POST',
					success: (data) => {
						console.log(data);
					}
				});
				console.log("提交成功！");
				this.isok = true;
			}else{
				this.isok = false;
			}
		},
		// 使用uni.navigateBack可以返回到原页面 （返回登录页面)
		backOne:function(){
			uni.navigateBack({
				delta:1
			});
		}
	}
}
